import React, { useContext, useState, useEffect } from 'react';
import { CartContext } from '../contexts/CartContext';
import { AuthContext } from '../contexts/AuthContext';
import api from '../api';
import { PaymentElement, useStripe, useElements, Elements } from '@stripe/react-stripe-js';
import { useNavigate } from 'react-router-dom';
import { loadStripe } from '@stripe/stripe-js';

const stripePromise = loadStripe(
  process.env.REACT_APP_STRIPE_PUBLISHABLE_KEY ||
  'pk_test_51SLaOfJlorzblLyDWDSrLT5VzN96yJC2ioMaaCO2wdr3A5ZfIKqo3v0SYOmXNp0SB7nS8abRbDefwQA6MGu9uRLx000TzdnnKe'
);

const Checkout = () => {
  const { cart, clearCart } = useContext(CartContext);
  const { user } = useContext(AuthContext);
  const [form, setForm] = useState({
    FirstName: '',
    MiddleName: '',
    LastName: '',
    Address: '',
    ZipCode: '',
    PhoneNumber: '',
    PaymentMethod: 'Cash on Delivery'
  });
  const [stripeClientSecret, setStripeClientSecret] = useState(null);
  const [message, setMessage] = useState(null);
  const navigate = useNavigate();

  const total = cart.reduce((sum, item) => sum + item.price * item.quantity, 0).toFixed(2);

  const handleCashOnDelivery = async () => {
    if (!form.FirstName || !form.LastName || !form.Address || !form.ZipCode || !form.PhoneNumber) {
      setMessage('Please fill in all required personal and address details.');
      return;
    }
    if (cart.length === 0) {
      setMessage('Your cart is empty. Please add items before checking out.');
      return;
    }
    if (!user?.id) {
      setMessage('Please login to place order');
      return;
    }

    try {
      const items = cart.filter(c => c.id != null).map(c => ({
        productId: String(c.id),
        quantity: c.quantity,
        price: c.price
      }));

      const payload = {
        userId: user.id,
        ...form,
        totalAmount: parseFloat(total),
        items
      };

      const res = await api.post('/checkout', payload);
      if (res?.data) {
        clearCart();
        setMessage('Order placed successfully (Cash).');
        try {
          await api.post('/orders', {
            userId: payload.userId,
            checkoutId: res.data._id,
            items,
            totalAmount: payload.totalAmount,
            shippingAddress: payload.Address
          });
        } catch (err) { /* ignore */ }
        navigate('/orders');
      }
    } catch (err) {
      console.error(err);
      setMessage('Error creating checkout: ' + (err?.response?.data?.message || err.message));
    }
  };

  useEffect(() => {
    if (form.PaymentMethod === 'Credit Card' && parseFloat(total) > 0 && user?.id) {
      api.post('/checkout/payment-intent', { amount: Math.round(parseFloat(total) * 100) })
        .then(res => setStripeClientSecret(res.data.clientSecret))
        .catch(err => {
          console.error('Payment intent error:', err);
          setMessage('Failed to initialize credit card payment: ' + (err?.response?.data?.message || err.message));
          setStripeClientSecret(null);
        });
    } else {
      setStripeClientSecret(null);
    }
  }, [form.PaymentMethod, total, user?.id]);

  return (
    <div className="p-8 min-h-screen">
      <h2 className="text-2xl font-bold mb-4">Checkout</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="bg-white p-4 rounded shadow">
          <div className="grid grid-cols-1 gap-3">
            <input placeholder="First Name" value={form.FirstName} onChange={(e) => setForm({...form, FirstName: e.target.value})} className="border rounded px-3 py-2" />
            <input placeholder="Middle Name" value={form.MiddleName} onChange={(e) => setForm({...form, MiddleName: e.target.value})} className="border rounded px-3 py-2" />
            <input placeholder="Last Name" value={form.LastName} onChange={(e) => setForm({...form, LastName: e.target.value})} className="border rounded px-3 py-2" />
            <input placeholder="Address" value={form.Address} onChange={(e) => setForm({...form, Address: e.target.value})} className="border rounded px-3 py-2" />
            <input placeholder="Zip Code" value={form.ZipCode} onChange={(e) => setForm({...form, ZipCode: e.target.value})} className="border rounded px-3 py-2" />
            <input placeholder="Phone Number" value={form.PhoneNumber} onChange={(e) => setForm({...form, PhoneNumber: e.target.value})} className="border rounded px-3 py-2" />
            <select value={form.PaymentMethod} onChange={(e) => setForm({...form, PaymentMethod: e.target.value})} className="border rounded px-3 py-2">
              <option value="Cash on Delivery">Cash on Delivery</option>
              <option value="Credit Card">Credit Card</option>
            </select>

            {form.PaymentMethod === 'Credit Card' ? (
              <PaymentForm form={form} total={total} clientSecret={stripeClientSecret} />
            ) : (
              <button type="button" onClick={handleCashOnDelivery} className="bg-blue-600 text-white px-3 py-2 rounded mt-2">Place Order</button>
            )}
            {message && <div className="mt-2 text-green-600">{message}</div>}
          </div>
        </div>

        <div className="bg-white p-4 rounded shadow">
          <h3 className="text-xl font-bold">Order Summary</h3>
          <ul className="mt-3 space-y-2">
            {cart.map(item => (
              <li key={item.id} className="flex items-center justify-between">
                <span>{item.name} x {item.quantity}</span>
                <span>${(item.price * item.quantity).toFixed(2)}</span>
              </li>
            ))}
          </ul>
          <div className="mt-4 font-bold">Total: ${total}</div>
        </div>
      </div>
    </div>
  );
};

const PaymentForm = ({ form, total, clientSecret }) => {
  if (!clientSecret) return null;
  return (
    <Elements stripe={stripePromise} options={{ clientSecret, appearance: { theme: 'stripe' } }}>
      <CreditCardForm form={form} total={total} clientSecret={clientSecret} />
    </Elements>
  );
};

const CreditCardForm = ({ form, total, clientSecret }) => {
  const stripe = useStripe();
  const elements = useElements();
  const { user } = useContext(AuthContext);
  const { cart, clearCart } = useContext(CartContext);
  const navigate = useNavigate();
  const [message, setMessage] = useState(null);

  const handleCreditCardSubmit = async (e) => {
    e.preventDefault();
    setMessage(null);

    if (!stripe || !elements) {
      setMessage('Stripe has not loaded correctly.');
      return;
    }
    if (!user?.id) {
      setMessage('User not logged in.');
      return;
    }
    if (cart.length === 0) {
      setMessage('Your cart is empty. Please add items before checking out.');
      return;
    }
    if (parseFloat(total) <= 0) {
      setMessage('Total amount must be greater than zero.');
      return;
    }
    if (!form.FirstName || !form.LastName || !form.Address || !form.ZipCode || !form.PhoneNumber) {
      setMessage('Please fill in all required fields.');
      return;
    }

    const { error: submitError } = await elements.submit();
    if (submitError) {
      setMessage(submitError.message);
      return;
    }

    try {
      const items = cart.filter(c => c.id != null).map(c => ({
        productId: String(c.id),
        quantity: c.quantity,
        price: c.price
      }));

      const payload = {
        userId: user.id,
        ...form,
        totalAmount: parseFloat(total),
        items
      };

      // First, create the checkout record
      const checkoutRes = await api.post('/checkout', payload);
      const checkoutId = checkoutRes.data._id;

      // Then confirm payment
      const confirmRes = await stripe.confirmPayment({
        elements,
        clientSecret,
        confirmParams: { return_url: `${window.location.origin}/orders` },
        redirect: 'if_required',
      });

      if (confirmRes.error) {
        setMessage(`Payment failed: ${confirmRes.error.message}`);
      } else if (confirmRes.paymentIntent && confirmRes.paymentIntent.status === 'succeeded') {
        // Create order after successful payment
        await api.post('/orders', {
          userId: user.id,
          checkoutId,
          items,
          totalAmount: parseFloat(total),
          shippingAddress: form.Address
        });
        clearCart();
        setMessage('Payment successful and order placed.');
        navigate('/orders');
      } else if (confirmRes.paymentIntent && confirmRes.paymentIntent.status === 'requires_action') {
        setMessage('Additional authentication required. Please complete the 3D Secure verification.');
      }
    } catch (err) {
      console.error('Checkout error:', err);
      setMessage('An error occurred: ' + (err?.response?.data?.message || err.message));
    }
  };

  return (
    <form onSubmit={handleCreditCardSubmit}>
      <PaymentElement />
      <button
        type="submit"
        disabled={!stripe || !elements || !clientSecret}
        className="bg-blue-600 text-white px-3 py-2 rounded mt-2 disabled:bg-gray-400"
      >
        Pay Now
      </button>
      {message && <div className="mt-2 text-red-600">{message}</div>}
    </form>
  );
};

export default Checkout;