import React, { useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import { AuthContext } from '../contexts/AuthContext';
import jariridLogo from '../assets/jaririd.png';

const Sidebar = ({ onSearch }) => {
  const [query, setQuery] = useState('');
  const { user, logout } = useContext(AuthContext);

  const handleSearch = (e) => {
    e.preventDefault();
    onSearch(query);
  };

  const handleLogout = () => {
    logout();
  };

  return (
    <aside className="w-64 h-screen bg-white shadow-lg fixed left-0 top-0 p-4 flex flex-col z-20">
      <div className="flex items-center gap-2 mb-6">
        <img src={jariridLogo} alt="logo" className="w-12 h-12 object-cover rounded-md" />
        <h1 className="text-xl font-semibold">Jarir Books</h1>
      </div>

      <nav className="flex-1">
        <ul className="space-y-2">
          <li><Link to="/" className="block px-2 py-2 rounded hover:bg-gray-100">Home</Link></li>
          <li><Link to="/cart" className="block px-2 py-2 rounded hover:bg-gray-100">Cart</Link></li>
          <li><Link to="/wishlist" className="block px-2 py-2 rounded hover:bg-gray-100">Wishlist</Link></li>
          <li><Link to="/orders" className="block px-2 py-2 rounded hover:bg-gray-100">My Orders</Link></li>
          {user?.isAdmin && (
            <li><Link to="/admin" className="block px-2 py-2 rounded hover:bg-gray-100">Admin</Link></li>
          )}
          {user ? (
            <li><button onClick={handleLogout} className="block w-full text-left px-2 py-2 rounded hover:bg-gray-100">Logout</button></li>
          ) : (
            <>
              <li><Link to="/login" className="block px-2 py-2 rounded hover:bg-gray-100">Login</Link></li>
              <li><Link to="/signup" className="block px-2 py-2 rounded hover:bg-gray-100">Signup</Link></li>
            </>
          )}
        </ul>
      </nav>

      <form onSubmit={handleSearch} className="mt-auto">
        <label htmlFor="search" className="sr-only">Search books</label>
        <div className="relative">
          <input
            id="search"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            placeholder="Search books..."
            className="w-full border rounded py-2 px-3 focus:outline-none focus:ring-2 focus:ring-blue-400"
          />
          <button className="absolute right-1 top-1/2 -translate-y-1/2 bg-blue-600 text-white px-3 py-1 rounded" type="submit">Search</button>
        </div>
      </form>
    </aside>
  );
};

export default Sidebar;
