import React from 'react';
import { Link } from 'react-router-dom';

const ProductCard = ({ product, onAddToCart, onToggleWishlist, isInWishlist = false }) => {
  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden transform hover:scale-105 transition-transform duration-300">
      <img src={product.image} alt={product.name} className="w-full h-56 object-cover" />
      <div className="p-4">
        <h3 className="text-lg font-semibold">{product.name}</h3>
        <p className="text-gray-500 mt-2">{product.description}</p>
        <div className="mt-4 flex items-center justify-between">
          <span className="font-bold text-xl">${product.price}</span>
          <div className="flex items-center gap-2">
            <Link to={`/product/${product.id}`} className="bg-gray-200 text-gray-800 px-3 py-1 rounded">View</Link>
            <button onClick={() => onAddToCart(product)} className="bg-blue-600 text-white px-3 py-1 rounded">Add</button>
            <button onClick={() => onToggleWishlist(product)} className={isInWishlist ? 'text-red-600' : 'text-gray-400'}></button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
