import React, { useContext, useMemo, useState, useEffect } from 'react';
import productsData from '../assets/products';
import api from '../api';
import ProductCard from '../components/ProductCard';
import { CartContext } from '../contexts/CartContext';

const Home = ({ search: searchProp = '' }) => {
  const { addToCart, toggleWishlist, wishlist, message } = useContext(CartContext);
  const search = searchProp || '';

  const [remoteProducts, setRemoteProducts] = useState(null);

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const res = await api.get('/products');
        setRemoteProducts(res.data.products || []);
      } catch (err) {
        // Merge fallback to local assets
        setRemoteProducts(null);
      }
    };
    fetchProducts();
  }, []);

  const filteredProducts = useMemo(() => {
    const query = (searchProp || search || '').toLowerCase();
    const list = Array.isArray(remoteProducts) && remoteProducts.length ? remoteProducts : productsData;
    return list.map(p => ({
      id: p._id || p.id,
      name: p.title || p.name,
      description: p.description || p.description,
      price: p.price || p.price,
      image: p.image || p.image,
      inStock: p.inStock || p.inStock,
    })).filter(p => p.name.toLowerCase().includes(query) || p.description.toLowerCase().includes(query));
  }, [search, searchProp, remoteProducts]);

  // Add animated hero
  return (
    <div className="min-h-screen p-8">
      <section className="mb-8">
        <div className="rounded-lg bg-gradient-to-r from-indigo-500 via-purple-500 to-pink-500 text-white p-8 shadow-lg transform hover:scale-[1.01] transition-transform duration-500">
          <h2 className="text-3xl font-bold">Discover Your Next Favorite Book</h2>
          <p className="mt-2 text-lg">Hand-picked titles and instant delivery  explore the collection now.</p>
        </div>
      </section>

      {message && <div className="mb-4 p-2 bg-green-100 text-green-800 rounded">{message}</div>}

      <section className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {filteredProducts.map(product => (
          <ProductCard key={product.id} product={product} onAddToCart={addToCart} onToggleWishlist={toggleWishlist} isInWishlist={!!wishlist.find(w => w.id === product.id)} />
        ))}
      </section>
    </div>
  );
};

export default Home;
